<template>
  <div class="sum-page">
    <el-form ref="form" :model="form" label-width="80px" :rules="rules">
      <el-form-item label="产品" prop="region">
        <el-select v-model="form.region" placeholder="请选择产品类型">
          <el-option label="无隔板过滤器" value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="厚度" prop="height">
        <el-select v-model="form.height" placeholder="请选择高度">
          <el-option label="90毫米(mm)" value="90"></el-option>
          <el-option label="70毫米(mm)" value="70"></el-option>
          <el-option label="50毫米(mm)" value="50"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="长度" prop="long">
        <el-input v-model="form.long"><template slot="append">毫米(mm)</template></el-input>
      </el-form-item>
      <el-form-item label="宽度" prop="width">
        <el-input v-model="form.width"><template slot="append">毫米(mm)</template></el-input>
      </el-form-item>
      <el-form-item label="结果" prop="result">
        <el-input v-model="form.result" :disabled="true"><template slot="append">元(￥)</template></el-input>
      </el-form-item>
      <el-form-item style="margin-left: 0">
        <el-button @click="resize">重置</el-button>
        <el-button type="primary" @click="onSubmit">计算</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
name: '',
data() {
  var checkNum = (rule, value, callback) => {
    if (!value) {
      return callback(new Error('不可为空'));
    }
    if (!Number.isInteger(parseInt(value))) {
      return callback(new Error('请输入数字值'));
    }
  };
  return {
    form: {
      region: '',
      long: '',
      width: '',
      height: '',
      result: ''
    },
    material: {
      '90': {
        ZheGao: 0.068,
        ABJiao: 0.37,
        Lv: 0.41
      },
      '70': {
        ZheGao: 0.048,
        ABJiao: 0.26,
        Lv: 0.32
      },
      '50': {
        ZheGao: 0.032,
        ABJiao: 0.24,
        Lv: 0.25
      }
    },
    config: [
      (long, width, options) => {
        var LvZhi =  long * 0.3 * options.ZheGao * 2 * width / 1000 * 0.09 * 60;
        console.log('滤纸:' + LvZhi);
        var ReRongJiao = long * 0.3 * options.ZheGao * 2 * width / 1000 * 0.09 * 20;
        console.log('热熔胶:' + ReRongJiao);
        var HuWang = long * width * 0.000025 * 2;
        console.log('护网:' + HuWang);
        var LvKuang = (long + width) * 2 * options.Lv * 0.025;
        console.log('铝框:' + LvKuang);
        var MiFengTiao = (long + width) * 0.002 * options.ABJiao * 15;
        console.log('密封条:' + MiFengTiao);
        return (LvZhi + ReRongJiao + HuWang + LvKuang + MiFengTiao).toFixed(2);
      }
    ],
    rules: {
      long: [
          {required: true, validator: checkNum, trigger: 'blur'}
      ],
      height: [
          {required: true, validator: checkNum, trigger: 'blur'}
      ],
      width: [
          {required: true, validator: checkNum, trigger: 'blur'}
      ],
      result: [
        {trigger: 'blur'}
      ],
      region: [
          {required: true, message: '请选择产品', trigger: 'change'}
      ],
    }
  }
},
  methods: {
    onSubmit()
    {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.sum();
        } else {
          console.log('参数错误！');
          return false;
        }
      });
    },
    sum() {
      var o = this.form;
      this.form.result = this.config[this.form.region](parseInt(o.long), parseInt(o.width), this.material[o.height]);
    },
    resize()
    {
      this.$refs['form'].resetFields()
    }
  }
}
</script>

<style>
  html,body {
    background: #eee;
  }
  .sum-page {
    width: 300px;
    margin: 0 auto;
    /*margin-top: 40%;*/
  }
</style>



